import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import throttle from 'lodash.throttle';

import { 
  fetchImages, 
  fetchMoreImages,
  cutDownSearchStore,
  fetchAmount,
  fetchOrder,
  fetchCategory,
  fetchColor,
  fetchImageType,
  fetchOrientation,
  fetchPage,
  fetchSearchText,
} from '../actions/searchAction';
import { fetchViewerOpen } from '../actions/imgViewerAction';
import { addToFavorites } from '../actions/appAddsAction';

import ImgViewer from './ImageViewer';

import { withStyles } from '@material-ui/core/styles';
import compose from 'recompose/compose';
import withWidth from '@material-ui/core/withWidth';

// import { GridList, GridListTile, IconButton, CircularProgress, } from '@material-ui/core';
import GridList from '@material-ui/core/GridList';
import GridListTile from '@material-ui/core/GridListTile';
import IconButton from '@material-ui/core/IconButton';
import CircularProgress from '@material-ui/core/CircularProgress';
// import { ArrowUpward, Close, } from '@material-ui/icons';
import StarBorderIcon from '@material-ui/icons/StarBorder';
import StarIcon from '@material-ui/icons/Star';

import ArrowUpward from '@material-ui/icons/ArrowUpward';
import Close from '@material-ui/icons/Close';

const styles = {
  rootComponent: {
    position: 'relative',
    top: 57,
    display: 'block',
    margin: '0px 1px',
    overflow: 'hidden',
  },
  barTitleTag: {
    paddingRight: '0.35em',
    cursor: 'pointer',
    textDecoration: 'none',
    color: '#eee',
    '&:hover': {
      textDecoration: 'underline',
    },
  },
  exceededLimitMessage: {
    margin: '10px 0',
    color: '#d50000',
    fontSize: '2em',
    width: '100%',
    textAlign: 'center',
  },
  gridList: {
    justifyContent: 'space-around',
    margin: '0px !important',
  },
  gridListTile: {
    padding: '0px !important',
    border: '2px solid rgba(0, 0, 0, 0)',
    borderRadius: '2px',
    transition: 'all 0.1s ease',
    '&:hover': {
      border: '2px solid rgba(0, 0, 0, 0.75)',
    },
    '&:hover .gridListTileBar': {
      backgroundColor: 'rgba(0, 0, 0, 0.85)',
    },
  },
  gridImg: {
    cursor: 'zoom-in',
  },
  gridListTileBarRoot: {
    height: 54,
    transition: 'all 0.1s ease',
    backgroundColor: 'rgba(0, 0, 0, 0.5)',
    boxSizing: 'border-box',
    bottom: 0,
    width: '100%',
    display: 'flex',
    position: 'absolute',
    alignItems: 'center',
    justifyContent: 'space-between',
  },
  gridListTileBarTitleWrap: {
    marginLeft: 12,
    marginRight: 7,
    color: '#fff',
    overflow: 'hidden',
    flexGrow: '0',
    width: '100%',
  },
  gridListTileBarTitle: {
    fontSize: '1em',
    lineHeight: '1.2em',
    overflow: 'hidden',
    whiteSpace: 'nowrap',
    textOverflow: 'ellipsis',
  },
  gridListTileBarSubtitle: {
    fontSize: '0.75em',
    lineHeight: '1.2em',
    overflow: 'hidden',
    whiteSpace: 'nowrap',
    textOverflow: 'ellipsis',
  },
  iconButtonWrap: {
    display: 'flex',  
    alignItems: 'center',
    justifyContent: 'flex-end',
  },
  iconButton:{
    width: '3em',
    height: '3em',
    fontSize: '0.8em',
    paddingRight: 4, 
    flexGrow: '0',
  },
  icon: {
    fontSize: '2em',
    color: '#fff',
    transition: 'all 0.1s linear',
    '&:hover': {
      fontSize: '3em',
    },
  },
  loadingBarRoot: {
    width: '100vw',
    textAlign: 'center',
  },
  loadingBarProgress: {
    margin: '10px 0',
    color: 'rgb(38, 50, 56)',
  },
  noMatchesWrapper: {
    display: 'flex',
    posititon: 'fixed',
    top: '0',
    left: '0',
    width: '100%',
    height: '80vh',
    alignItems: 'center',
    justifyContent: 'center',
  },
  noMatches: {
    width: '90vw',
    textAlign: 'center',
    fontSize: '2.5em',
    fontWeight: 'bold',
    color: '#aaa',
  },
  socialData: {
    color: '#fff',
    fontSize: '1.2em',
    flexGrow: '0',
    marginRight: '-3px',
    float: 'right',
  },
  waitApiResponseImages: {
    display: 'flex',
    height: '80vh',
    justifyContent: 'center',
    alignItems: 'center',
  },
  loadPreviousImagesRoot: {
    position: 'absolute',
    top: '0.8em',
    left: 0,
    right: 0,
    margin: '0 auto',
    width: '55%',
    maxWidth: 300,
    minWidth: 250,
    height: 40,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    zIndex: 1,
    transition: 'all 0.5s ease',
    animation: 'loadPreviousImagesButton 0.5s ease-in',
  },
  loadPreviousImagesButton: {
    width: '90%',
    height: '100%',
    transition: 'all 0.25s ease',
    backgroundColor: 'rgba(255,235,59,0.95)',
    borderRadius: 2,
    boxShadow: '0px 5px 5px -3px rgba(0, 0, 0, 0.2), 0px 8px 10px 1px rgba(0, 0, 0, 0.14), 0px 3px 14px 2px rgba(0, 0, 0, 0.12)',
    fontSize: 19,
    '&:hover' : {
      backgroundColor: 'rgba(255,235,59,1)',
      color: 'rgba(0,0,0,0.7)',
    }
  },
  loadPreviousImagesCloseButton: {
    width: '1em',
    height: '1em',
    margin: '-0.5em 0 0 -0.5em',
    alignSelf: 'start',
    '& > span': {
      backgroundColor: 'rgba(38, 50, 56, 0.8)',
      width: '1em',
      height: '1em',
      borderRadius: '50%', 
      margin: '0 auto',
    },
    '& > span > svg': {
      fill: '#fff',
      width: '0.7em',
      height: '0.7em',
    },
    '&:hover': {
      '& > span': {
        backgroundColor: 'rgba(38, 50, 56, 1)',
      },
      '& > span > svg': {
        fill: 'rgba(255,235,59,1)',
      },
    }
  },
  loadPreviousImagesCloseButtonMobile: {
    width: '2em',
    height: '2em',
    margin: '-1em 0 0 -1em',
  }
};

// for calculating whole document height & windowHeight (in handleScroll())
const body = document.body;
const html = document.documentElement; 
let docHeight;
const updateDocHeight = () => { docHeight = Math.max(body.scrollHeight, body.offsetHeight, html.clientHeight, html.scrollHeight, html.offsetHeight)}

class SearchResults extends Component {
  constructor(props) {
    super(props);
    this.state = {
      showLoadPreviousImagesButton: true,
    };
    this.handleScroll = this.handleScroll.bind(this);
    this.handleScrollThrottled = throttle(this.handleScroll, 250);
    this.handleSearchPath = this.handleSearchPath.bind(this);
    this.updateSearchPath = this.updateSearchPath.bind(this);
  } 

  componentDidMount() {
    let { params } = this.props.match;
    (params.query !== undefined) ? this.handleSearchPath(params.query) : this.updateSearchPath();
    (params.page !== undefined) ? this.props.fetchImages(Number(params.page)) : (this.props.fetchAmount(this.props.width), this.props.fetchImages());
    window.addEventListener('scroll', this.handleScrollThrottled);
    // calculate whole document height
    updateDocHeight();
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.handleScrollThrottled);
    this.props.cutDownSearchStore();
  }

  getSnapshotBeforeUpdate(prevProps, prevState) {
    const { images, waitApiResponseMoreImages } = this.props;
    if (this.props.width !== prevProps.width) {
      // return current user position
      let userPosition = window.pageYOffset / docHeight;
      return userPosition;
    }
    if ((images.length !== prevProps.images.length) && (waitApiResponseMoreImages.previous !== prevProps.waitApiResponseMoreImages.previous)) {
      return docHeight
    }
    return null;
  }

  componentDidUpdate = (prevProps, prevState, snapshot) => {
    // update whole document height
    updateDocHeight();
    const {
      images,
      searchText,
      order,
      orientation,
      imageType,
      category,
      color,
      page,
      amount,
      width,
      waitApiResponseMoreImages,
      open,
      waitApiResponseImages,
      match,
    } = this.props;
    if (width !== prevProps.width) {
      // forceUpdate with timeout is needed for correct calculation of images fit (inside GridListTile) & user position
      setTimeout(() => {
        let userPosition = snapshot * docHeight;
        window.scrollTo(0, userPosition);
        this.forceUpdate();
      }, 100);
    }
    if (
      !open && ((searchText !== prevProps.searchText) || 
      (order !== prevProps.order) || 
      (orientation !== prevProps.orientation) || 
      (imageType !== prevProps.imageType) || 
      (category !== prevProps.category) || 
      ((color.length !== prevProps.color.length) || (color[0] !== prevProps.color[0])) || 
      (page !== prevProps.page))
    ) {
      this.updateSearchPath();
    }
    // update path when user click on 'Home' button (in Drawer) from 'SearchResults'-page
    ((prevProps.match.params.query !== undefined) && (this.props.location.pathname === '/findpicture/')) ? this.updateSearchPath() : null;
    // update searchPath on fetching previous images pages
    ((images.length !== prevProps.images.length) && (waitApiResponseMoreImages.previous !== prevProps.waitApiResponseMoreImages.previous)) ? window.scrollBy(0, (docHeight-snapshot)) : null;
    (((open !== prevProps.open) && !open) || ((waitApiResponseMoreImages.next !== prevProps.waitApiResponseMoreImages.next) && !waitApiResponseMoreImages.next)) ? this.handleScroll() : null;
    ((waitApiResponseImages !== prevProps.waitApiResponseImages) && !waitApiResponseImages && (match.params.viewer !== undefined)) ? this.props.fetchViewerOpen(true, ((Number(match.params.viewer) - 1)-(page * amount - amount))) : null;
  }

  handleSearchPath = (searchPath) => {
    let searchParameters = searchPath.split('&');
    searchParameters.forEach((parameter, index) => {
      let filter = parameter.split('=');
      if (filter.length === 1){
        (index === 0) ? this.props.fetchSearchText(filter[0]) : null;
      } else {
        if (filter[0] === 'q') {
          this.props.fetchAmount(null, Number(filter[1]));
        } else {
          let filterHandler = 'fetch' + filter[0][0].toUpperCase() + filter[0].slice(1);
          let filterValue = (filter[0] !== 'color') ? filter[1] : (filter[1]).split(',');
          this.props[filterHandler](filterValue);
        }
      }
    })
  }

  updateSearchPath = () => {
    const {
      searchText,
      order,
      orientation,
      imageType,
      category,
      color,
      page,
      amount,
      history,
      match,
    } = this.props;
    let customFilterValues = '';
    (searchText !== '') ? (customFilterValues += searchText) : null;
    (order !== 'popular') ? (customFilterValues += '&order=' + order) : null;
    (orientation !== 'horizontal') ? (customFilterValues += '&orientation=' + orientation) : null;
    (imageType !== 'photo') ? (customFilterValues += '&imageType=' + imageType) : null;
    (category !== '') ? (customFilterValues += '&category=' + category) : null;
    (color[0] !== null) ? (customFilterValues += '&color=' + color) : null;
    
    if ((page === 1) && (match.params.viewer !== undefined)) {
      customFilterValues += '&q=' + amount + '/' + page
    } else if ((page !== 1) || (match.params.viewer !== undefined)) {
      customFilterValues += '&q=' + amount + '/' + page
    }
    
    (match.params.viewer !== undefined) ? (customFilterValues += '/' + match.params.viewer) : null;

    if (customFilterValues !== '') {
      let historyPath = '/findpicture/q_' + customFilterValues;
      history.push(historyPath);
    } else {
      history.push('/findpicture');
    }
  }

  handleScroll() {
    const { page, amount, images, totalHits, history, match, waitApiResponseMoreImages } = this.props;
    // get height of the window 
    const windowHeight = "innerHeight" in window ? window.innerHeight : document.documentElement.offsetHeight;
    // calcualte path page height (*/*/:page?)
    const pathPageHeight = docHeight / (images.length / amount);
    // calculate currentWindowYOffset
    const windowYOffset = window.pageYOffset + (windowHeight - 200);
    // calculate currentPathPageYOffset parameters
    let currentPathPageStart,
        currentPathPageEnd;
    let currentPathPage = Number((match.params.page !== undefined) ? match.params.page : 1);
    // set currentPathPage start&end bounds depends on number of loaded pages from API
    const fetchedPages = Math.ceil(images.length / amount)
    if (fetchedPages === page) { // pages from API was loaded from the 1st one
      currentPathPageStart = pathPageHeight * (currentPathPage - 1);
    } else {
      currentPathPageStart = pathPageHeight * (currentPathPage - 1 - (page - fetchedPages))
    }
    currentPathPageEnd = currentPathPageStart + pathPageHeight;
    let pathQuery = (match.params.query === undefined) ? '' : match.params.query;
    if (!this.props.open) {
      if ((windowYOffset > currentPathPageEnd)) {
        let newPath = `/findpicture/q_${pathQuery}/${currentPathPage + 1}`;
        history.push(newPath);
      } else if (windowYOffset < currentPathPageStart) {
        let newPath = `/findpicture/q_${pathQuery}/${currentPathPage - 1}`;
        history.push(newPath);
      } else if (match.params.viewer !== undefined) {
        let newPath = `/findpicture/q_${pathQuery}/${currentPathPage}`;
        history.push(newPath);
      }
    }
    
    if ((totalHits > images.length) && ((amount * page) < totalHits) && (!this.props.waitApiResponseImages)) {
      // calculate bottom of window
      const windowBottom = windowHeight + window.pageYOffset;
      // if window bottom is less then 60px to document bottom then add more images
      if (((windowBottom - docHeight) > -60) && !waitApiResponseMoreImages.next) {
        this.props.fetchPage(this.props.page + 1);
        this.props.fetchMoreImages();
      }
    }
  };

  handleImgOpen = (index) => {
    this.props.fetchViewerOpen(true, index);
    (this.props.page === 1) ? (setTimeout(() => {this.updateSearchPath()}, 100)) : null;
  }

  // +++
  handleOnTagClick = (tag) => {
    this.props.fetchSearchText(tag);
    this.props.fetchPage(1);
    this.props.fetchImages();
  }

  // +++
  formatData = (data) => {
    let formattedData;
    switch (true) {
      case (data < 1000):
        formattedData = data;
        break;
      case (data < 1000000):
        formattedData = (data / 1000).toFixed(0) + 'K';
        break;
      default:
        formattedData = (data / 1000000).toFixed(0) + 'M';
        break;
    }
    return formattedData
  }
  
  render() {
    let imageListContent,
        imageViewer,
        cellHeight,
        loadingBar,
        _gridCols;

    const { 
      images,
      totalHits,
      searchText,
      favorites,
      amount,
      page,
      classes,
      width,
      mobileWithTouch,
      waitApiResponseImages,
      waitApiResponseMoreImages,
      windowTop,
      match,
    } = this.props;

    const _loadingBar = (
      < div className={classes.loadingBarRoot} >
        <CircularProgress
          className={classes.loadingBarProgress}
          size={50}
        />
      </div >
    );

    loadingBar = () => {
      switch (true) {
        case ((totalHits > images.length) && (images.length < 500) && ((amount * page) < totalHits)):
          return _loadingBar
        case ((totalHits === images.length) || ((amount * page) > totalHits)):
          return null
        default:
          return <div className={classes.exceededLimitMessage}>API limit is exceeded</div> 
      }
    }

    const _loadPreviousImages = () => {
      if (!waitApiResponseMoreImages.previous) {
        return (
          <div className={`${classes.loadPreviousImagesRoot} ${windowTop ? null : 'hidden'}`}>
            <IconButton
              classes={{
                root: classes.loadPreviousImagesButton,
              }}
              onClick={() => { this.props.fetchMoreImages(false, false, (match.params.page - 1)) }}
              title='load previous images'
            >
              <ArrowUpward />
              <span> load previous images </span>
              {(width !== 'xs') ? <ArrowUpward /> : null}
            </IconButton>
            <IconButton
              className={`${classes.loadPreviousImagesCloseButton} ${mobileWithTouch ? classes.loadPreviousImagesCloseButtonMobile : null}`}
              onClick={() => this.setState({ showLoadPreviousImagesButton: false })}
              disableRipple
              title='close'
              size='large'
            >
              <Close />
            </IconButton>
          </div>
        )
      } else {
        return (
          _loadingBar
        )
      }
    };
    
    if (this.props.orientation === 'vertical') {
      cellHeight = 270;
      switch (true) {
        case (width === 'xl'):
          _gridCols = 8
          break;
        case (width === 'lg'):
          _gridCols = 6
          break;
        case (width === 'md'):
          _gridCols = 4
          break;
        case (width === 'sm'):
          _gridCols = 3
          break;
        default:
          _gridCols = 2
          break;
      }
    } else {
      cellHeight = 180;
      switch (true) {
        case (width === 'xl'):
          _gridCols = 5
          break;
        case (width === 'lg'):
          _gridCols = 4
          break;
        case (width === 'md'):
          _gridCols = 3
          break;
        case (width === 'sm'):
          _gridCols = 2
          break;
        default:
          _gridCols = 1
          break;
      }
    }

    let isFavorite = (imgId) => favorites.findIndex(favImg => favImg.id === imgId);

    if (waitApiResponseImages) {
      imageListContent = (
        <div className={classes.waitApiResponseImages}>
          {_loadingBar}
        </div>
      )
    } else if (images.length) {      
      imageListContent = (
        <div>
          {((Math.ceil(images.length / amount) < page) && this.state.showLoadPreviousImagesButton) ? _loadPreviousImages() : null}
          <GridList
            className={classes.gridList}
            cols={_gridCols} 
            cellHeight={cellHeight}
          >
            {images.map((img, index) => ( 
              <GridListTile 
                className={classes.gridListTile}
                key={index + '_' + img.id} 
              >
                <img
                  className={classes.gridImg}
                  src={img.webformatURL}
                  alt={img.tags}
                  onClick={() => this.handleImgOpen(index)}
                />
                <div className={`${classes.gridListTileBarRoot} ${mobileWithTouch ? null : 'gridListTileBar'}`}>
                  <div className={classes.gridListTileBarTitleWrap}> 
                    <div className={classes.gridListTileBarTitle}>
                      {
                        (() => {
                          let tagArray;
                          if (img.tags.split(',').length > 1) {
                            tagArray = img.tags.split(',')
                          } else {
                            tagArray = img.tags.split(' ')
                          }
                          return (
                            tagArray.map((tag, tagIndex)=> (
                              <span
                              className={classes.barTitleTag}
                              key={tagIndex}
                              title={`search for '${tag.trim()}'`}
                              onClick={() => this.handleOnTagClick(tag.trim())}
                          >
                            #{tag.trim()}
                          </span>
                        )))})()
                      }
                    </div>
                    <div className={classes.gridListTileBarSubtitle}>
                      {
                        <span>
                          by: <a className={classes.barTitleTag}
                            href={`https://pixabay.com/users/${img.user}-${img.user_id}/`}
                            target='_blank'
                            title={`'${img.user}' profile at Pixabay`}
                          >
                            {img.user}
                          </a>
                        </span>
                      }
                    </div>
                  </div>
                  <div>
                    <span className={classes.socialData}>
                      {this.formatData((!~isFavorite(img.id) ? img.favorites : (img.favorites+1)))}
                    </span>
                  </div>
                    <IconButton
                      className={classes.iconButton}
                    onClick={() => this.props.addToFavorites(index)}
                      title={!~isFavorite(img.id) ? 'add to Favorites' : 'remove from Favorites'}
                    >
                      {!~isFavorite(img.id) ? <StarBorderIcon className={classes.icon} /> : <StarIcon className={classes.icon} />}
                    </IconButton>
                </div>
              </GridListTile >
            ))}
          </GridList>
          {loadingBar()}
        </div>
      )
    } else if (searchText) {
      imageListContent = (
        <div className={classes.noMatchesWrapper}>
          <div className={classes.noMatches}>
            no matches for "{searchText}"
          </div>
        </div>
      )
    }

    if (this.props.open) {
      imageViewer = <ImgViewer match={match} history={this.props.history}/>
    }
    
    return (
      <div className={classes.rootComponent}>
        {imageListContent}
        {imageViewer}
      </div>
    )
  }
}

SearchResults.propTypes = {
  fetchImages: PropTypes.func,
  fetchMoreImages: PropTypes.func,
  fetchPage: PropTypes.func,
  fetchViewerOpen: PropTypes.func,
  fetchSearchText: PropTypes.func,
  fetchAmount: PropTypes.func,
  fetchOrder: PropTypes.func,
  fetchCategory: PropTypes.func,
  fetchColor: PropTypes.func,
  fetchImageType: PropTypes.func,
  fetchOrientation: PropTypes.func,
  cutDownSearchStore: PropTypes.func,
  addToFavorites: PropTypes.func,
  images: PropTypes.array,
  totalHits: PropTypes.number,
  page: PropTypes.number,
  amount: PropTypes.number,
  orientation: PropTypes.string,
  open: PropTypes.bool,
  searchText: PropTypes.string,
  favorites: PropTypes.array,
  classes: PropTypes.object.isRequired,
  width: PropTypes.string.isRequired,
  mobileWithTouch: PropTypes.bool,
  waitApiResponseImages: PropTypes.bool.isRequired,
  waitApiResponseMoreImages: PropTypes.object,
  currentImgInd: PropTypes.number,
  windowTop: PropTypes.bool,
}

const mapStateToProps = state => ({
  images: state.search.images,
  totalHits: state.search.total,
  page: state.search.page,
  orientation: state.search.orientation,
  open: state.imgViewer.open,
  searchText: state.search.searchText,
  favorites: state.appAdds.favorites,
  mobileWithTouch: state.appAdds.mobileWithTouch,
  waitApiResponseImages: state.search.waitApiResponseImages,
  waitApiResponseMoreImages: state.search.waitApiResponseMoreImages,
  order: state.search.order,
  imageType: state.search.imageType,
  category: state.search.category,
  color: state.search.color,
  amount: state.search.amount,
  currentImgInd: state.imgViewer.currentImgInd,
  windowTop: state.appAdds.windowTop,
})

const mapDispatchToProps = {
  fetchImages,
  fetchMoreImages,
  fetchPage,
  fetchViewerOpen,
  cutDownSearchStore,
  addToFavorites,
  fetchSearchText,
  fetchAmount,
  fetchOrder,
  fetchCategory,
  fetchColor,
  fetchImageType,
  fetchOrientation,
}

export default connect(mapStateToProps, mapDispatchToProps)(compose(withStyles(styles), withWidth(), )(SearchResults))


