import React, { Component } from 'react';
import PropTypes from 'prop-types';

import { withStyles } from '@material-ui/core/styles';

// import { Drawer, List, ListItem, ListItemIcon, ListItemText, Divider, } from '@material-ui/core/';

import Drawer from '@material-ui/core/Drawer';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Divider from '@material-ui/core/Divider';
// import { Close, Home, Grade, History, } from '@material-ui/icons/';
import Close from '@material-ui/icons/Close';
import Home from '@material-ui/icons/Home';
import Grade from '@material-ui/icons/Grade';
import History from '@material-ui/icons/History';
import ContentLink from '@material-ui/icons/Link';

import { Link } from 'react-router-dom';

const styles = {
  drawerPaper: {
    minHeight: '100vh',
    height: 'auto',
  },
  drawerHeader: {
    backgroundColor: 'rgb(38,50,56)',
    height: 57,
    paddingRight: 0,
  },
  headerIcon: {
    color: '#fff',
    cuheaderIconrsor: 'pointer',
  },
  headerText: {
    fontSize: '1.5em',
    color: '#fff',
  },
  itemText: {
    fontSize: '1.1em',
  },
};

class MenuDrawer extends Component {
  constructor(props) {
    super(props);
    this.handleListItemClick = this.handleListItemClick.bind(this);
  }

  handleListItemClick = () => {
    window.scrollTo(0, 0);  
    this.props.onChange(false);
  };

  render() {
    const { classes } = this.props;

    let navigationCloseIconLeft, 
        navigationCloseIconRight;

    let _navigationCloseIcon = (
      <ListItemIcon>
        <Close className={classes.headerIcon} onClick={() => this.props.onChange(false)} />
      </ListItemIcon>
    )

    switch (true) {
      case (this.props.side==='right'):
        navigationCloseIconRight = _navigationCloseIcon;
        break;
      case (this.props.side==='left'):
        navigationCloseIconLeft = _navigationCloseIcon;
        break;
      default:
        break;
    }
    return (
      <Drawer
        classes={{
          paper:classes.drawerPaper
        }}
        anchor={this.props.side}
        open={this.props.value}
        onClose={() => this.props.onChange(false)}
      >
        <ListItem className={classes.drawerHeader}>
          {navigationCloseIconLeft}
          <ListItemText className={classes.headerText} primary="FindPicture" disableTypography />
          {navigationCloseIconRight}
        </ListItem>
        <Divider />
        <List>
          <ListItem button component={Link} to='/findpicture/' onClick={this.handleListItemClick}>
            <ListItemIcon>
              <Home />
            </ListItemIcon>
            <ListItemText className={classes.itemText} primary="Home" disableTypography />
          </ListItem>
          <ListItem button component={Link} to='/findpicture/favorites' onClick={this.handleListItemClick}>
            <ListItemIcon>
              <Grade />
            </ListItemIcon>
            <ListItemText className={classes.itemText} primary="Favorites" disableTypography />
          </ListItem>
          <ListItem button component={Link} to='/findpicture/recentlywatched' onClick={this.handleListItemClick}>
            <ListItemIcon>
              <History />
            </ListItemIcon>
            <ListItemText className={classes.itemText} primary="Recently Watched" disableTypography />
          </ListItem>
        </List>
        <Divider />
        <List>
          <ListItem button component='a' href='https://gslav27.github.io/other/parallax_1.html' target='_blank' onClick={() => this.props.onChange(false)}>
            <ListItemIcon>
              <ContentLink />
            </ListItemIcon>
            <ListItemText className={classes.itemText} primary="Parallax" disableTypography />
          </ListItem>
          <ListItem button component='a' href='https://gslav27.github.io' target='_blank' onClick={() => this.props.onChange(false)}>
            <ListItemIcon>
              <ContentLink />
            </ListItemIcon>
            <ListItemText className={classes.itemText} primary="gslav27.github.io" disableTypography />
          </ListItem>
          <ListItem button component='a' href='https://pixabay.com' target='_blank' onClick={() => this.props.onChange(false)}>
            <ListItemIcon>
              <ContentLink />
            </ListItemIcon>
            <ListItemText className={classes.itemText} primary="Pixabay.com" disableTypography />
          </ListItem>
        </List>
        <Divider />
      </Drawer>
    )
  }
}

MenuDrawer.propTypes = {
  value: PropTypes.bool.isRequired,
  onChange: PropTypes.func.isRequired,
  side: PropTypes.string,
  classes: PropTypes.object.isRequired,
}

export default withStyles(styles)(MenuDrawer)


