import React, { Component } from 'react';
import './App.css';

import { Route, Switch } from 'react-router-dom';
import { MuiThemeProvider } from '@material-ui/core/styles';
import ThemeDefault from './ThemeDefault'

import NavBar from './components/NavBar';
import SearchResults from './components/SearchResults';
import RecentlyWatched from './components/RecentlyWatched';
import Favorites from './components/Favorites';

class App extends Component {
  render() {
    return (
      <MuiThemeProvider theme={ThemeDefault}>
        <div>
          <Route path="/findpicture" component={NavBar} />
          <Switch>
            <Route exact path="/findpicture" component={SearchResults} />
            <Route path="/findpicture/recentlywatched" component={RecentlyWatched} />
            <Route path="/findpicture/favorites" component={Favorites} />
            <Route path="/findpicture/q_:query?/:page?/:viewer?" component={SearchResults} />
          </Switch>
        </div >
      </MuiThemeProvider>
    );
  }
}

export default App;


